:root {
  --bg: #F56C24;
  --bg: #3C2E3D;
  --bg: #33334D;
  --brand-icon: #B2F3F9;
  --brand: #FFDC88;
  --selection-bg: #B2F3F9;
}

body {
  background-color: #33334D;
  background-color: var(--bg);
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 2rem;
}

::selection {
  background: #B2F3F9;
  background: var(--selection-bg);
}
